<template>
  <div id="app">
    <div class="rangeSelection">
      一些说明：<br />
      <br />
      默认选择日期：2020-8-2~2020-8-6，<br />如果不支持范围选择，则默认为2020-8-2<br />
      <br />
      设定的可选范围是2020-8-1~2020-10-15<br />
      <br />
      是否支持日期范围选择:<button @click="changeRangeSelection">{{ rangeSelection }}</button>
      <br />
      补充一下，该按钮只能改变是否支持范围选择，并不能改变默认值。
      <br />
    </div>
    <cDatePicker :defaultDate="defaultDate" :minDate="minDate" :maxDate="maxDate" :rangeSelection="rangeSelection" @change="change"></cDatePicker>
  </div>
</template>
<script>
import cDatePicker from "./components/DatePicker";
import * as utils from "./src/Util";
export default {
  components: {
    cDatePicker,
  },
  data() {
    return {
      //设置日期的时候，注意月数与month的值相差1

      //设置默认值，如果rangeSelection为false,会忽略end只接收start
      defaultDate: {
        start: new Date(2020, 7, 2),
        end: new Date(2020, 7, 6),
      },

      //设置日期选择范围
      minDate: new Date(2020, 7, 1),
      maxDate: new Date(2020, 9, 15),

      //设置是否支持时间段选择
      rangeSelection: false,
    };
  },
  methods: {
    change(rangeDate) {
      let { year: sy, month: sm, day: sd } = utils.getYearMonthDay(rangeDate.start);
      let { year: ey, month: em, day: ed } = utils.getYearMonthDay(rangeDate.end);
      this.defaultDate.start = new Date(sy, sm, sd);
      this.defaultDate.end = new Date(ey, em, ed);
    },
    changeRangeSelection() {
      if (this.rangeSelection) {
        this.rangeSelection = false;
      } else {
        this.rangeSelection = true;
      }
    },
  },
};
</script>

<style lang="stylus">
@import "./src/common.styl";
#app
  width 100vw;
  height 100vh;
  margin 100px;
  .rangeSelection
    margin-bottom 10px;
    button
      margin 10px;
      padding 2px;
      border 1px solid #b3b3b3;
      cursor pointer;
</style>
