<template>
  <div class="c-date-picker" v-click-outside>
    <cInputBox class="cInputBox" :targetValue="res"></cInputBox>
    <cCalendar :rangeDate="defaultDate" :minDate="minDate" :maxDate="maxDate" :range="rangeSelection" @getResult="getResult" v-if="isShow"> </cCalendar>
  </div>
</template>

<script>
import cInputBox from "./InputBox";
import cCalendar from "./Calendar";
import * as utils from "../src/Util";
export default {
  components: {
    cInputBox,
    cCalendar,
  },
  props: {
    defaultDate: {
      type: Object,
      required: true,
    },
    minDate: {
      type: Date,
      required: true,
    },
    maxDate: {
      type: Date,
      required: true,
    },
    rangeSelection: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      res: this.setResult(this.defaultDate),
      isShow: false,
    };
  },
  directives: {
    clickOutside: {
      //绑定元素el，bindings是一个对象，vnode指虚拟节点
      //bind和unbind只执行一次
      bind(el, bindings, vnode) {
        let handler = (e) => {
          if (el.contains(e.target)) {
            //判断当前面板是否已经显示
            if (!vnode.context.isShow) {
              vnode.context.focus();
            }
          } else {
            if (vnode.context.isShow) {
              vnode.context.blur();
            }
          }
        };
        el.handler = handler;
        document.addEventListener("click", handler);
      },
      unbind(el) {
        document.removeEventListener("click", el.handler);
      },
    },
  },
  methods: {
    //控制面板
    focus() {
      this.isShow = true;
    },
    //控制面板
    blur() {
      this.isShow = false;
    },
    //计算结果
    setResult(objDate) {
      let { year: sy, month: sm, day: sd } = utils.getYearMonthDay(objDate.start);
      let { year: ey, month: em, day: ed } = utils.getYearMonthDay(objDate.end);
      if (this.rangeSelection) {
        return `${sy}-${sm + 1}-${sd}~${ey}-${em + 1}-${ed}`;
      } else {
        return `${sy}-${sm + 1}-${sd}`;
      }
    },
    //返回改变的结果
    getResult(rangeDate) {
      this.res = this.setResult(rangeDate);
      this.$emit("change", rangeDate);
    },
  },
};
</script>

<style lang="stylus" scoped>

.c-date-picker
  width 200px;
  .cInputBox
    width 100%;
    border 1px solid #b3b3b3;
    cursor pointer
  .cInputBox /deep/ .img-box
    background url('../src/img/calendar.png') no-repeat;
</style>
