<template>
  <div class="calendar" v-if="isShow">
    <div class="calendar-nav">
      <cSelectBox class="yearsSelect" @select="setYear" :targetValue="currentShowDate.getFullYear() + '年'" :itemList="yearsList()"> </cSelectBox>
      <cSubAddSelectBox class="monthsSelect" @sub="subMonth" @add="addMonth" @select="setMonth" :targetValue="currentShowDate.getMonth() + 1 + '月'" :itemList="monthsList()"> </cSubAddSelectBox>
      <button class="confirm" @click="changeDate">确定</button>
    </div>
    <div class="calendar-content">
      <div class="titleArr">
        <span class="titleCells" v-for="i in 7" :key="i">
          {{ titleArr[i - 1] }}
        </span>
      </div>
      <div class="days">
        <div class="rows" v-for="i in 6" :key="i">
          <span @click="chooseDate(dateArr[(i - 1) * 7 + (j - 1)])" class="cells" :class="[{ notCurrentMonth: !currentMonth(dateArr[(i - 1) * 7 + (j - 1)]) }, { select: isSelect && dateArr[(i - 1) * 7 + (j - 1)] >= currentChooseDate.start && dateArr[(i - 1) * 7 + (j - 1)] <= currentChooseDate.end }, { weekend: dateArr[(i - 1) * 7 + (j - 1)].getDay() == 0 || dateArr[(i - 1) * 7 + (j - 1)].getDay() == 6 }, { rangeOut: isRangeOut(dateArr[(i - 1) * 7 + (j - 1)]) }]" v-for="j in 7" :key="j">
            {{ dateArr[(i - 1) * 7 + (j - 1)].getDate() }}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import cSelectBox from "./SelectBox";
import cSubAddSelectBox from "./SubAddSelectBox";
import * as utils from "../src/Util";
export default {
  components: {
    cSelectBox,
    cSubAddSelectBox,
  },
  props: {
    rangeDate: {
      type: Object,
      required: true,
    },
    minDate: {
      type: Date,
      required: true,
    },
    maxDate: {
      type: Date,
      required: true,
    },
    range: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    let { year: sy, month: sm, day: sd } = utils.getYearMonthDay(this.rangeDate.start);
    let { year: ey, month: em, day: ed } = utils.getYearMonthDay(this.rangeDate.end);
    return {
      minShowYear: 1900,
      maxShowYear: 2050,
      titleArr: ["一", "二", "三", "四", "五", "六", "日"],
      dateArr: [],
      currentChooseDate: {
        start: new Date(sy, sm, sd),
        end: this.range ? new Date(ey, em, ed) : new Date(sy, sm, sd),
      },
      position: "start",
      currentShowDate: new Date(sy, sm, sd),
      isShow: true,
      isSelect: true,
      border: false,
    };
  },
  methods: {
    //显示日历年月
    yearsList() {
      let yearsArr = [];
      for (let i = this.minShowYear; i < this.maxShowYear; i++) {
        yearsArr.push(i + "年");
      }
      return yearsArr;
    },
    monthsList() {
      let monthsArr = [];
      for (let i = 1; i < 13; i++) {
        monthsArr.push(i + "月");
      }
      return monthsArr;
    },

    //计算daysArr,记录显示的42个天数
    showDays(date) {
      //查找周几
      let { year, month } = utils.getYearMonthDay(date);
      let currentFirstDay = new Date(year, month, 1);
      let week = currentFirstDay.getDay() || 7;
      let start = currentFirstDay - (week - 1) * 24 * 60 * 60 * 1000;
      //计算显示天数
      this.dateArr = [];
      for (let i = 0; i < 42; i++) {
        this.dateArr.push(new Date(start + i * 24 * 60 * 60 * 1000));
      }
    },
    //判断是否为当前月，参数为一个日期对象
    currentMonth(date) {
      let { year, month } = utils.getYearMonthDay(this.currentShowDate);
      //对象解构赋值
      let { year: y, month: m } = utils.getYearMonthDay(date);
      return year == y && month == m;
    },
    //选择日期
    chooseDate(date) {
      if (this.isRangeOut(date)) {
        this.isSelect = false;
        alert("该日期超过选择范围");
      } else {
        this.isSelect = true;
        let { year, month, day } = utils.getYearMonthDay(date);
        //根据是否可以选择时间段来进行分类
        if (!this.range) {
          //如果点击的对象不为当月，则进行跳转
          if (!this.currentMonth(date)) {
            this.showDays(date);
            this.currentShowDate = new Date(year, month, day);
          }
          this.currentChooseDate.start = new Date(year, month, day);
          this.currentChooseDate.end = new Date(year, month, day);
        } else {
          if (this.position == "start") {
            this.currentChooseDate.start = new Date(year, month, day);
            this.currentChooseDate.end = new Date(year, month, day);
            this.position = "end";
          } else {
            this.currentChooseDate.end = new Date(year, month, day);
            this.position = "start";
            if (this.currentChooseDate.end < this.currentChooseDate.start) {
              this.currentChooseDate.start = new Date(year, month, day);
              this.currentChooseDate.end = new Date(year, month, day);
              this.position = "end";
            }
          }
        }
      }
    },

    //判断是否在范围外
    isRangeOut(date) {
      if (date < this.minDate || date > this.maxDate) {
        return true;
      } else {
        return false;
      }
    },

    //通过确定按钮将选择结果返回
    changeDate() {
      this.$emit("getResult", this.currentChooseDate);
      this.isShow = false;
    },
    //通过select改变年
    setYear(value) {
      this.currentShowDate.setFullYear(parseInt(value));
      this.showDays(this.currentShowDate);
    },
    //通过select改变月
    setMonth(value) {
      this.currentShowDate.setMonth(parseInt(value) - 1);
      this.showDays(this.currentShowDate);
    },
    //通过sub改变月
    subMonth() {
      let month = this.currentShowDate.getMonth();
      this.currentShowDate.setMonth(month - 1);
      this.showDays(this.currentShowDate);
    },
    //通过add改变月
    addMonth() {
      let month = this.currentShowDate.getMonth();
      this.currentShowDate.setMonth(month + 1);
      this.showDays(this.currentShowDate);
    },
  },
  created() {
    //初始化的时候，显示默认显示日期的面板
    this.showDays(this.currentShowDate);
  },
};
</script>

<style lang="stylus" scoped>
.calendar
  width 300px;
  border 1px solid #b3b3b3;
  box-shadow  1px 1px 1px #b3b3b3;
  .calendar-nav
    margin 10px;
    display flex;
    justify-content space-between;
    .yearsSelect
      width 80px;
    .monthsSelect
      width 110px;
    .confirm
      width 60px;
      border 1px solid #b3b3b3;

  .calendar-content
    margin 10px;
    border 1px solid #b3b3b3;
    .titleArr
      padding:0 7px;
      background #eee;
      border-bottom 1px solid #b3b3b3;
      display flex;
      .titleCells
        display inline-block;
        width 38px;
        height 38px;
        text-align center;
        line-height 38px;
      .titleCells:nth-child(n+6)
        color #d35400;
        font-font-weight bold;
    .days
      .rows
        width 280px;
        padding:0 7px;
        display flex;
        .cells
          display inline-block;
          width 38px;
          height 38px;
          text-align center;
          line-height 38px;
          box-sizing border-box;
          cursor pointer;
        .hover
          border 1px solid #b3b3b3;
        .weekend
          color #d35400;
        .notCurrentMonth
          color gray;
        .select
          color white;
          font-font-weight bold;
          background-color #c0392b;
        .rangeOut
          color #f6e58d;
</style>
