<template>
  <!-- 项目列表 -->
  <div class="cpn-item-list">
    <ul @click="target">
      <li v-for="(item, index) in itemList" :key="index">
        {{ item }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    itemList: {
      type: Array,
      default: () => ["暂无"],
    },
  },
  methods: {
    target(e) {
      this.$emit("target", e.target.innerText);
    },
  },
};
</script>

<style lang="stylus" scoped>
.cpn-item-list
  width 100%;
  overflow hidden;
  ul
    height 230px;
    border 1px solid #b3b3b3;
    background-color white;
    overflow auto;
    li
      cursor pointer;
      padding-left 4px;

    li:hover
      background-color #b3b3b3;
</style>
