<template>
  <div class="cpn-sub-add-select-box">
    <button class="left" @click="sub"></button>
    <cSelectBox class="monthsSelect" :targetValue="targetValue" :itemList="itemList" @select="select"> </cSelectBox>
    <button class="right" @click="add"></button>
  </div>
</template>

<script>
import cSelectBox from "./SelectBox";

export default {
  props: {
    targetValue: {
      type: String,
      required: true,
    },
    itemList: {
      type: Array,
      default: () => ["暂无"],
    },
  },
  components: {
    cSelectBox,
  },
  methods: {
    select(value) {
      this.$emit("select", value);
    },
    sub() {
      this.$emit("sub");
    },
    add() {
      this.$emit("add");
    },
  },
};
</script>

<style lang="stylus" scoped>
.cpn-sub-add-select-box
  width 100%;
  border 1px solid #b3b3b3;
  display flex;
  justify-content space-between;
  align-items center;
  .left
  .right
    width 20px;
    height 20px;
    background url('../src/img/arrow_left.png') no-repeat;
    cursor pointer;
  .monthsSelect
    flex 1;
  .monthsSelect /deep/ .cInputBox
    border-top none;
    border-bottom none;
  .right
    background url('../src/img/arrow_right.png') no-repeat;
</style>
