<template>
  <div class="cpn-select-box">
    <cInputBox class="cInputBox" @click.native="showList" :targetValue="targetValue"> </cInputBox>
    <cItemList class="cItemList" v-show="isShowList" :itemList="itemList" @target="setTargetValue"> </cItemList>
  </div>
</template>

<script>
import cInputBox from "./InputBox";
import cItemList from "./ItemList";
export default {
  props: {
    targetValue: {
      type: String,
      required: true,
    },
    itemList: {
      type: Array,
      default: () => ["暂无"],
    },
  },
  data() {
    return {
      isShowList: false,
    };
  },
  components: {
    cInputBox,
    cItemList,
  },
  methods: {
    showList() {
      if (this.isShowList) {
        this.isShowList = false;
      } else {
        this.isShowList = true;
      }
    },
    setTargetValue(value) {
      this.$emit("select", value);
      this.isShowList = false;
    },
  },
};
</script>

<style lang="stylus" scoped>
.cpn-select-box
  width 100%;
  position relative;
  .cInputBox
    cursor pointer;
    border 1px solid #b3b3b3;
  .cItemList
    position absolute;
    left 0;
    top 22px;
</style>
